import React, { Component } from 'react'
import Header from '../Header'
import Analyze from './Analyze'
import Footer from '../Footer'
import Results from '../Results'
import Spinner from '../Spinner'
import store from '../Store'
import { API } from '../../Constants'
import { Redirect } from 'react-router-dom'

class Detail extends Component {

  constructor(props) {
    super(props);
  }

  state = {
    working: false,
    problem: false,
    error: "Unable analyze your files",
    newImageSet: "",
    reprocessed: false
  }

  componentDidMount() {
    store.listen(this.processUpdated, "PROCESS");
    this.componentWillReceiveProps(this.props);
  }

  componentWillReceiveProps = (nextProps) => {
    const { match } = nextProps;
    if (match.params.images == "reprocess") {
      setTimeout(function () {
        this.reprocess();
      }.bind(this), 100);
    } else if (match.params.selected == "reprocess") {
      setTimeout(function (param) {
        this.reprocess(param);
      }.bind(this)(match.params.images), 100);
    }

    if (this.state.reprocessed) {
      this.setState({ reprocessed: false });
    }
  }

  processUpdated = (ev) => {
    console.log("processUpdated = (ev)", ev);
    switch (ev) {
      case "STARTED":
        this.setState({ working: true });
        break;
      case "FINISHED":
        this.setState({ working: false });
        break;
    }

    if (ev.indexOf("TRIGGER") > -1) {
      let imageSet = ev.replace("TRIGGER-", "");
      this.reprocess(imageSet);
    }
  }

  reprocess = (imageSetParam) => {
    if (this.state.working) {
      return;
    }

    let that = this,
      imageSet = imageSetParam ? imageSetParam : store.user().imageSet,
      images = store.images(imageSet), urls = [];
    if (images == null || images.length == 0) {
      console.warn("Nothing to reprocess", imageSet);
      return;
    }

    for (let i = 0; i < images.length; i++) {
      let img = images[i];
      urls.push(img.Url);
    }

    let data = {
      "Urls": urls,
      "PublishEnvironment": store.user().environment
    }, imageSetId = store.processStarted();

    this.setState({ newImageSet: imageSetId });

    if (store.hasUser()) {
      data["AudienceSize"] = store.user().fanCount;
    }

    fetch(API + '/page/sortimagesurls', {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      body: JSON.stringify(data)
    }).then(function (response) {
      if (response.ok) {
        return response.json();
      }
      return false;
    }).then(function (data) {
      if (data && data.length > 0) {
        store.processFinished(that.state.newImageSet, data);
        setTimeout(function () {
          this.setState({ reprocessed: true });
        }.bind(that),
          300);
      } else {
        store.processFailed(that.state.newImageSet);
      }
    }).catch(function (error) {
      store.processFailed(that.state.newImageSet);
      console.log('request failed', error);
    });
  }

  render() {
    const { match } = this.props;
    const { reprocessed, working, problem, error, newImageSet } = this.state
    let selection = match.params.selected;
    if (selection === undefined || selection == null) {
      let imageSetParam = match.params.images, images = store.images(imageSetParam);
      if (images != null && images.length > 0) {
        let best = images[0];
        for (let i = 0; i < images.length; i++) {
          if (best.Result.Rank < images[i].Result.Rank) {
            best = images[i];
          }
        }
        selection = best.Id;
      }
    }
    return (
      <div>
        {reprocessed && (
          <Redirect to={"/detail/" + newImageSet} />
        )}
        <Header />
        {working && (
          <div className="reprocess" style={{ textAlign: "center" }}>
            <span >Analyzing your photos please wait.</span>
            <Spinner />
          </div>
        )}
        <Analyze imageSet={match.params.images} selected={selection} />
        <div className="container">
          <div className="row">
            <div className="col">
              <h2>Other Evaluated Images &nbsp;&nbsp;<span style={{ fontSize: "12px", fontStyle: "italic" }}>Click to see details</span></h2>
            </div>
          </div>
          <Results howMany={3} imageSet={match.params.images} selected={selection} />
        </div>
        <Footer />
      </div>
    )
  }
}

export default Detail




