import React from 'react'
import { Link } from 'react-router-dom'

const Footer = () => (
  <div className="footer" style={{ textAlign: "right", padding: "10px 30px", marginTop: "25px" }}>
    <Link to="/detail" style={{ display: "none", color: "#fff", fontSize: "12px" }}>
      Go to Detail(Just to test)&nbsp;&nbsp;&bull;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    </Link>
    <Link to="/privacy" style={{ color: "#fff", fontSize: "16px" }}>
      Privacy Policy
    </Link>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <Link to="/"><img src="logo-st.png" style={{ maxHeight: "25px", marginRight: "50px" }} /></Link>
  </div>
)

export default Footer



