import React, { Component } from 'react'
import Header from '../Header'
import Footer from '../Footer'

class Privacy extends Component {

  render() {
    return (
      <div>
        <Header />
        <div className="container">
          <div className="row">
            <div className="col">
              <h2 style={{ marginLeft: 0 }}>Privacy Policy</h2>
              <h3 style={{ marginLeft: 0 }}> About the LikeMeter Project</h3>
              <p>The purpose of the LikeMeter project is to predict the performance of content before actually sharing by employinh deep learning techniques. Such techniques are not limited to analyze visual content but also competitor analyses ..etc</p>

              <h3 style={{ marginLeft: 0 }}>Terms of Use</h3>
              <p>The use of photos by uploading or fetched through Instagram API must obey all laws, including applicable copyright and privacy laws.</p>
              <h3 style={{ marginLeft: 0 }}>Contact</h3>
              <p>If you have questions, please contact likemeter@pangea.co</p>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}

export default Privacy




