import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'

import fetchJsonp from 'fetch-jsonp'
import store from './Store'
import { API } from '../Constants'

class Instagram extends Component {
  state = {
    user: null,
    photos: null,
    working: false,
    token: null,
    ourToken: null,
    hasUser: false,
    hasLoggedIn: false
  }

  componentDidMount() {

  }

  loginOk = (token) => {
    var that = this;

    fetchJsonp("https://api.instagram.com/v1/users/self/?access_token=" + token)
      .then(function (response) {
        return response.json()
      }).then(function (data) {
        that.tryUser(data.data, token);
        return data;
      }).catch(function (ex) {
        console.log('parsing failed', ex)
      });
  }

  loginWithPageId = (pageId) => {
    return fetch(API + '/user/loginwithpageid?pageid=' + pageId)
      .then(function (response) {
        if (response.ok) {
          return response.json();
        }
        return false;
      })
      .then((data) => {
        if (data) {
          //this.setState({ ourToken: data.access_token, hasUser: true });
          this.setState({ ourToken: data.access_token });
          //this.update(data.access_token, 0, 150);
        }
        return data;
      })
      .catch(function (error) {
        console.log('request failed', error)
        return error;
      });
  }

  tryUser = (item, token) => {
    ////data.bio,data.counts.followed_by ,(follows,media),id,full_name,profile_picture,username
    let user = {
      ourToken: this.state.ourToken,
      token: token,
      fanCount: item.counts.followed_by,
      pageId: item.id,
      pageName: item.full_name + " (" + item.username + ")",
      pageImage: item.profile_picture,
      username: item.username,
      id: item.id,
      environment: "Instagram",
      shouldReprocess: false
    }, that = this;

    if (store.user() != null) {
      user.imageSet = store.user().imageSet;
    }

    let fanCountChanged = false;
    if (store.hasUser()) {
      //user.fanCount = store.user().fanCount + 100;
      fanCountChanged = store.user().fanCount != user.fanCount;
    } else {
      user.shouldReprocess = true;
    }

    this.loginWithPageId(item.id).then((data) => {
      if (data) {
        user.organizationId = data.organizationid;
        user.ourToken = data.access_token;
        if (fanCountChanged) {
          user.shouldReprocess = true;
          this.update(data.access_token, 0, user.fanCount);
        }
        this.setState({ user: user, hasUser: true });
        store.user(user);
        return data;
      }
      return false;
    }).then((hasUser) => {
      if (!hasUser) {
        this.createUser(user);
      }
      that.busy(false);
    });
  }

  createUser = (user) => {
    console.log("createUser", user);
    this.busy();
    this.signup(user.pageName, user.token, user.pageId, user.fanCount)
      .then((data) => {
        console.log(data);
        if (data) {
          user.organizationId = data.Id;
        }
        this.setState({ user: user, hasUser: true });
        store.user(user);
        this.busy(false);
      });
  }

  signup = (name, token, pageId, fanCount) => {
    let data = {
      'Name': name,
      'PageId': pageId,
      'PageToken': token,
      'PageType': 0,
      'FanCount': fanCount
    }, that = this;

    return fetch(API + '/user/organization', {
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      method: "post",
      body: JSON.stringify(data)
    }).then(function (response) {
      //console.log('signup succeeded with JSON response', response);
      if (response.ok) {
        return response.json();
      }
      return false;
    }).catch(function (error) {
      console.log('request failed', error)
      return error;
    });
  }

  update = (token, id, fanCount) => {
    let data = {
      "FanCount": fanCount
    };
    return fetch(API + '/user/updateorganization', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json; charset=utf-8"
      },
      method: "POST",
      body: JSON.stringify(data)
    })
      .then(function (response) {
        return response.ok;
      }).catch(function (error) {
        return error;
      });
  }


  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  login = (e) => {
    if (this.state.working) {
      e.preventDefault();
      this.loginOk(document.getElementById("testt").value);
      return;
    }

    this.busy();
    var that = this;
    //let returnUrl = 'http://localhost:8000';
    //TODO: change this before version deployment, and automate that shit please.
    let returnUrl = 'http://t.burakaltundal.com';
    let instagramUrl = "https://www.instagram.com/oauth/authorize/?client_id=bd52243c2b244f17828d74c5e35f0ffa&redirect_uri=" + returnUrl + "&response_type=token&scope=public_content";
    window.openedWindow = window.open(instagramUrl, 'authWindow');
  }


  logout = () => {

  }

  render() {
    const { photos, working } = this.state;
    return (
      <div style={{ textAlign: "center" }} >
        <input type="hidden" id="testt" />
        <button className="btn btn-primary btn-radius ins-connect" id="insbtn" onClick={this.login}  >
          <i className="fa fa-instagram"></i>&nbsp;&nbsp;&nbsp;&nbsp;CONNECT WITH INSTAGRAM</button> <br />
        {working && (<Spinner />)}
      </div>
    )
  }
}
export default Instagram



