import React, { Component } from 'react'
import { EventEmitter } from 'events'

let CHANGE_EVENT = "change", USER_EVENT = "user", IMAGES = "images";

class Store extends EventEmitter {

  constructor() {
    super();
    this.state = { user: null, images: {}, sets: {} };
    if (this.isSaved()) {
      try {
        this.deserialize();
      } catch (err) {
        console.log(err)
      }
    }

    console.log("State preloaded", this.state);
  }

  anId() {
    return Math.random().toString(36).slice(2);
  }

  deserialize() {
    this.deserializeUser();
    this.deserializeImages();
  }

  serialize() {
    this.serializeImages();
    this.serializeUser();
  }

  serializeUser() {
    //console.log("serializeUser()", JSON.stringify(this.state.user.imageSet));
    let str = JSON.stringify(this.state.user), base64Str = window.btoa(encodeURIComponent(str));
    var d = new Date();
    d.setTime(d.getTime() + (20 * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toUTCString();
    document.cookie = "likeostateuser=" + base64Str + ";" + expires + ";path=/";
  }

  serializeImages() {
    let str = JSON.stringify(this.state.images), base64Str = window.btoa(encodeURIComponent(str));
    localStorage.setItem("images", str);

    var d = new Date();
    d.setTime(d.getTime() + (20 * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toUTCString();
    document.cookie = "likeostate=" + base64Str + ";" + expires + ";path=/";
  }

  deserializeImages() {
    if (localStorage.getItem("images") !== null) {
      this.state.images = JSON.parse(localStorage.getItem("images"));
    }
    return;
    if (document.cookie.indexOf("likeostate=") >= 0) {
      let base64Value = document.cookie.split('likeostate=')[1];
      let strValue = decodeURIComponent(window.atob(base64Value.split(";")[0]));
      this.state.images = JSON.parse(strValue);
    }

  }

  deserializeUser() {
    if (document.cookie.indexOf("likeostateuser=") >= 0) {
      let base64Value = document.cookie.split('likeostateuser=')[1];
      let strValue = decodeURIComponent(window.atob(base64Value.split(";")[0]));
      this.state.user = JSON.parse(strValue);
    }
  }

  isSaved() {
    return document.cookie.indexOf("likeostate=") >= 0;
  }

  user(u) {
    if (u === undefined) {
      return this.state.user;
    }

    this.state.user = u;
    this.emitChange(USER_EVENT);
  }

  hasUser() {
    return (this.state.user != null && this.state.user.id > 0)
  }

  images(keyset, imgs) {
    if (imgs === undefined) {
      return this.state.images[keyset] ? this.state.images[keyset] : [];
    }
    console.warn("images(keyset,imgs)");
  }

  imageFromSet(keyset, imageId) {
    let images = this.state.images[keyset];
    if (images == null)
      return null;

    for (let i = 0; i < images.length; i++) {
      if (images[i].Id == imageId) {
        return images[i];
      }
    }
    return null;
  }

  processRestart(imageSet) {
    this.emitChange("PROCESS", "TRIGGER-" + imageSet);
  }

  processStarted() {
    let imageSet = this.anId();
    this.state.sets[imageSet] = { status: 0 };
    this.emitChange("PROCESS", "STARTED");
    return imageSet;
  }

  processFinished(imageSet, images) {
    console.log("processFinished(imageSet,images)", imageSet, images.length);
    this.state.sets[imageSet].status = 1;
    if (images.length > 0) {
      let usersImageset = "-";
      if (this.state.user == null) {
        this.state.user = {};
      } else {
        usersImageset = this.state.user.imageSet;
        if (this.state.user.shouldReprocess === true) {
          this.state.user.shouldReprocess = false;
        }
      }

      for (var k in this.state.images) {
        if (k != usersImageset) {
          delete (this.state.images[k]);
        }
      }
      this.state.images[imageSet] = images;
      this.emitChange(IMAGES);
      this.state.user.imageSet = imageSet;
      this.emitChange(USER_EVENT);
    }
    this.emitChange("PROCESS", "FINISHED");
    return this.state.images[imageSet];
  }

  processFailed(imageSet) {
    this.state.sets[imageSet].status = -1;
    this.emitChange("PROCESS", "FAILED");
    this.emitChange("PROCESS", "FINISHED");
    console.log("processFailed(imageSet)", imageSet);
  }

  getState() {
    return this.state;
  }

  logout() {
    if (this.state.user != null) {
      this.state.user = { imageSet: this.state.user.imageSet, shouldReprocess: true };
    }
    this.emitChange(USER_EVENT);
  }

  emitChange(eventName, data) {
    console.log("Emiting change", eventName);
    this.emit(eventName ? eventName : CHANGE_EVENT, data);
    if (eventName == USER_EVENT) {
      this.serializeUser();
    } else if (eventName == IMAGES) {
      this.serializeImages();
    }
  }

  listen(callback, eventName) {
    console.log("Listening change", eventName);
    this.on(eventName ? eventName : CHANGE_EVENT, callback);
  }

  unListen(callback, eventName) {
    console.log("Removing listener", eventName);
    this.removeListener(eventName ? eventName : CHANGE_EVENT, callback);
  }
}

Store.dispatchToken = null;
const storeSingle = new Store();

export default storeSingle;


