import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'

import fetchJsonp from 'fetch-jsonp'
import store from './Store'
import { API } from '../Constants'
import { Redirect } from 'react-router-dom'

class InstaFeed extends Component {
  state = {
    photos: null,
    working: false,
    processed: false,
    processedImageSet: null
  }

  componentDidMount() {

  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  search = (e) => {
    this.busy();
    let that = this, tagToSearch = document.getElementById("instatag").value,
      urlToFetch = "https://api.instagram.com/v1/users/self/media/recent?access_token=" + store.user().token;
    //urlToFetch = "https://api.instagram.com/v1/tags/" + tagToSearch + "/media/recent?access_token=" + this.state.token;

    fetchJsonp(urlToFetch)
      .then(function (response) {
        return response.json()
      }).then(function (res) {
        let all = [];
        for (var i = 0; i < res.data.length; i++) {
          let ins = res.data[i];
          let photo = { title: ins.caption != null ? ins.caption.text : "", image: ins.images.standard_resolution.url, likeCount: ins.likes.count, link: ins.link };
          all.push(photo);
        }

        let theLimit = 12;
        return all.length > theLimit ? all.slice(0, theLimit) : all;
      }).then(function (photos) {
        that.setState({ photos: photos, working: false });
      }).catch(function (ex) {
        console.log('parsing failed', ex);
        that.setState({ working: false });
        return ex;
      });
  }

  processSelected = () => {
    const { photos } = this.state;
    //this.busy();
    let urls = [], that = this;

    for (var i = 0; i < photos.length; i++) {
      let img = photos[i];
      if (img.selected === true) {
        urls.push(img.image);
      }
    }

    let data = {
      "Urls": urls,
      "PublishEnvironment": store.user().environment
    }

    if (store.hasUser()) {
      data["AudienceSize"] = store.user().fanCount;
    }

    let imagsetId = store.processStarted();
    this.setState({ processedImageSet: imagsetId, photos: null });

    fetch(API + '/page/sortimagesurls', {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      body: JSON.stringify(data)
    }).then(function (response) {
      if (response.ok) {
        return response.json();
      }
      return false;
    }).then(function (data) {
      if (data && data.length > 0) {
        store.processFinished(imagsetId, data);
        that.setState({ processed: true });
      }
      //that.busy(false);
    }).catch(function (error) {
      console.log('request failed', error);
      //that.busy(false);
    });
  }

  select = (ind, item) => {
    this.state.photos[ind].selected = !(this.state.photos[ind].selected);
    this.forceUpdate();
  }

  render() {
    const { photos, working, processed, processedImageSet } = this.state;
    const { hasUser } = this.props;
    let hasLoggedIn = hasUser ? store.user().environment == "Instagram" : false;

    return (
      <div style={{ textAlign: "center" }} >
        {processed && (
          <Redirect to={"/detail/" + processedImageSet} />
        )}
        {hasLoggedIn && (
          <div>
            <div className="input-group" style={{ marginTop: "15px", marginBottom: "15px" }}>
              <input type="text" id="instatag" className="form-control" placeholder="Tag to search on Instagram" />
              <span className="input-group-addon" onClick={this.search}><i className="fa fa-search"></i></span>
            </div>
            <button className="btn btn-primary btn-radius ins-connect" onClick={this.processSelected} style={{ marginBottom: "15px" }}>
              <i className="fa fa-instagram"></i>&nbsp;&nbsp;&nbsp;&nbsp;ANALYZE INSTAGRAM PHOTOS
              {working && (<Spinner inline={true} />)}
            </button>
            {photos && (
              <div>
                {photos.map((item, ind) => (
                  <div onClick={() => this.select(ind, item)} key={ind}
                    className={'f-img with-border with-shadow with-radius ' + (item.selected ? 'selected' : '')}
                    style={{ cursor: "pointer", display: "inline-block", backgroundImage: `url(${item.image})`, margin: "0 1%", width: "30%", minHeight: "150px", borderColor: item.selected ? "#e4405f" : "rgba(85, 85, 85, 0.3)" }} ></div>
                ))}
              </div>
            )}
          </div>
        )
        }
      </div>
    )
  }
}
InstaFeed.propTypes = {
  hasUser: PropTypes.bool
}

export default InstaFeed



